import VueRouter from "vue-router";
import Vue from "vue";
import axios, {AxiosInstance, AxiosResponse} from 'axios';




export class Service{
    $router: VueRouter | undefined;
    $vue: Vue | undefined;
    public service: AxiosInstance;

    constructor() {
        let service = axios.create({
            //baseURL:'https://complice-backend.test/api/admin-envios/',
            baseURL:'https://api.todienvios.com/api/admin-envios/',
            headers :{
                'Accept': 'application/json'
            }
        });
        service.interceptors.response.use(this.handleSuccess, this.handleError);
        this.service = service;
    }

    handleSuccess(response: any) {
        return response;
    }

    handleError = (error: { response: AxiosResponse  }) => {
        switch (error.response.status) {
            case 401:
                this.redirectTo(document, '/')
                break;
            case 404:
                this.$vue?.$alert(error.response.data.message, 'Error', "error")
                break;
            default:
                this.$vue?.$alert(error.response.data.message, 'Error', "error")
                console.log(error.response.data.message)
                break;
        }
        return Promise.reject(error)
    }

    redirectTo = (document:any, path:string) => {
        document.location = path
    }
}
