import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'
import Admin from '../layouts/Admin.vue'
import Auth from '../layouts/Auth.vue'

import store from '@/store'

Vue.use(VueRouter)

const routes: Array<RouteConfig> = [
  {
    path: '/',
    redirect: '/dashboard',
    component: Admin,
    children:[
      {
        path: '/dashboard',
        name:'dashboard',
        meta:{
          auth: true,
          title: 'dashboard'

        },
        component: () => import('../views/Dashboard.vue')
      },
      {
        path: '/servicios',
        name:'services',
        meta:{
          auth: true,
          title: 'servicios'
        },
        component: () => import('../views/Services.vue')
      },
      {
        path: '/mensajeros',
        name:'mensajeros',
        meta:{
          auth: true,
          title: 'mensajeros'
        },
        component: () => import('../views/Mensajeros.vue')
      },
      {
        path: '/mensajeros/:id',
        name:'mensajero-detail',
        meta:{
          auth: true,
          title: 'mensajero'
        },
        component: () => import('../views/MensajeroDetail.vue')
      },
      {
        path: '/clientes',
        name:'clientes',
        meta:{
          auth: true,
          title: 'Clientes'
        },
        component: () => import('../views/Clientes.vue')
      },
      {
        path: '/issues',
        name:'issues',
        meta:{
          auth: true,
          title: 'Issues'
        },
        component: () => import('../views/issues/Issue.vue')
      },
      {
        path: '/issues/:id/chat',
        name:'issues-chat',
        meta:{
          auth: true,
          title: 'chat'
        },
        component: () => import('../views/issues/IssueChat.vue')
      },
      {
        path: '/issues/:id/room',
        name:'issues-room',
        meta:{
          auth: true,
          title: 'chat issue'
        },
        component: () => import('../views/issues/IssueRoom.vue')
      },
      {
        path: '/mensajeros-activos',
        name:'active-courier',
        meta:{
          auth: true,
          title: 'Mensajeros activos'
        },
        component: () => import('../views/couriers/ActiveCouriers.vue')
      },
      {
        path: '/mensajeros-activos/:id/mapa',
        name:'active-courier-map',
        meta:{
          auth: true,
          title: 'Mensajero mapa'
        },
        component: () => import('../views/couriers/ActiveCourierMap.vue')
      },
      {
        path: '/contabilidad',
        name:'accounting',
        meta:{
          auth: true,
          title: 'Contabilidad'
        },
        component: () => import('../views/Accounting.vue')
      },
      {
        path: '/accounting/:id',
        name:'accounting-detail',
        meta:{
          auth: true,
          title: 'Contabilidad'
        },
        component: () => import('../views/AccountingDetail.vue')
      },
      {
        path: '/servicios/:id/map',
        name:'service-map',
        meta:{
          auth: true,
          title: 'Tracking'
        },
        component: () => import('../views/ServiceMap.vue')
      },
      {
        path: '/contabilidad/servicios',
        name:'accounting-service',
        meta:{
          auth: true,
          title: 'Contabiliad de servicios'
        },
        component: () => import('../views/AccountingService.vue')
      },
      {
        path: '/config/global',
        name:'config-global',
        meta:{
          auth: true,
          title: 'Configuración'
        },
        component: () => import('../views/ConfigGlobal.vue')
      },
      {
        path: '/config/services',
        name:'config-services',
        meta:{
          auth: true,
          title: 'Configuración de servicios'
        },
        component: () => import('../views/ConfigService.vue')
      },
      {
        path: '/config/tarifas/:id',
        name:'config-tarifas',
        meta:{
          auth: true,
          title: 'Configuración de tarifas'
        },
        component: () => import('../views/ConfigRateService.vue')
      },

    ]

  },
  {
    path: '/auth',
    component:Auth,
    redirect: '/login',
    children:[
      {
        path: '/login',
        name:'login',
        component: () => import('../views/Login.vue'),
        meta: {
          title: 'ingreso'

        }
      },
      {
        path: '/recupera-contrasena',
        name:'forgot-password',
        component: () => import('../views/ForgotPassword.vue'),
        meta: {
          title: 'Recupera contraseña'
        }
      },
      {
        path: '/restablece-contrasena/:token',
        name:'reset-password',
        component: () => import('../views/ResetPassword.vue'),
        meta: {
          title: 'Restablece contraseña'
        }
      },
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.auth)) {
    if (store.state.profile == undefined) {
      next('login');
    } else {
      next();
    }
  } else {
    next();
  }
});

export default router
