import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from "vuex-persistedstate";
import router from '../router';
import {firebaseAction, vuexfireMutations} from "vuexfire";
import {db} from "@/plugins/db";

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    profile: undefined,
    dboys: [],
    dboyId: {}
  },
  mutations: {
    setUser (state: any, payload: any) {
      state.profile = payload
    },
    resetDboy(state: any){
      state.dboyId = {}
    },
    ...vuexfireMutations,
  },
  actions: {
    async login({commit}: any, payload: any) {
      commit('setUser', payload);
      await router.push({name: 'dashboard'})
    },

    async logout({commit}: any) {
      commit('setUser', undefined);
      await router.push({name: 'login'})
    },

    bindDboys: firebaseAction(({ bindFirebaseRef }) => {
      // return the promise returned by `bindFirebaseRef`
      return bindFirebaseRef('dboys', db.ref('co/dboys'))
    }),

    bindDboysID: firebaseAction(({ bindFirebaseRef }, id: string) => {
      // return the promise returned by `bindFirebaseRef`
      return bindFirebaseRef('dboyId', db.ref('co/dboys').child(id))
    }),

    resetDboyId({commit}: any) {
      commit('resetDboy');
    },


  },
  modules: {
  },
  plugins:[createPersistedState()]
})
