import Vue from 'vue'
import { ApolloClient } from 'apollo-client'
import { HttpLink } from 'apollo-link-http'
import { InMemoryCache } from 'apollo-cache-inmemory'
// New Imports
import {ApolloLink} from 'apollo-link'
//import Pusher from "pusher-js"


import VueApollo from 'vue-apollo'

import { createUploadLink } from 'apollo-upload-client'

// @ts-ignore
import Echo from "laravel-echo";
import {createLighthouseSubscriptionLink} from "@/echo_custom_link";


const urlPath ="https://api.todienvios.com/graphql"
const urlPathDev ="https://complice-backend.test/graphql"
const urlEchoDev ="localhost:6001"
const urlEcho ="https://socket.todienvios.com"

const prod = true;


//import PusherLink from "@/apollo_link";
// Install the vue plugin like before
Vue.use(VueApollo)

const echoClient = new Echo({
    broadcaster: 'socket.io',
    host: prod ? urlEcho : urlEchoDev,
    forceTLS: true,
    wsPort: 443,
    disableStats: true,
    encrypted: true,



});


