function getToken() :string{
    let vuex = localStorage.getItem('vuex')

    if(vuex){
       return JSON.parse(vuex).profile?.token ?? ''
    }else{
        return ''
    }
}


const httpLink = new HttpLink({
    // You should use an absolute URL here
    uri: prod ?  urlPath : urlPathDev,
    headers:{
        Authorization: `Bearer ${getToken()}`
    }
})



// const wsLink = new WebSocketLink({
//     uri: 'ws://complicesas-todobackend.test:6001/app/sadasdsad?protocol=7&client=js&version=7.0.3&flash=false',
//     options: {
//       reconnect: true,
//     },
//   })


//   console.log('WebSocketLink link')
//   console.log(wsLink)


//   const link = split(
//     // split based on operation type
//     ({ query }) => {
//       const definition = getMainDefinition(query)
//       return definition.kind === 'OperationDefinition' &&
//         definition.operation === 'subscription'
//     },
//     wsLink,
//     httpLink
//   )



 const link = ApolloLink.from([createLighthouseSubscriptionLink(echoClient),createUploadLink({uri: prod ?  urlPath : urlPathDev,
     headers:{
         Authorization: `Bearer ${getToken()}`
     }}), httpLink,  ]);


// Create the apollo client
export const apolloClient = new ApolloClient({
    link,
    cache: new InMemoryCache(),
    connectToDevTools: true,

})

export const apolloProvider = new VueApollo({
    defaultClient: apolloClient,
})

