import Vue from 'vue'
import App from '@/App.vue'
import '@/registerServiceWorker'
import router from '@/router'
import store from '@/store'
import { Service } from '@/services/Service';

import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'
// Import Bootstrap an BootstrapVue CSS files (order is important)
import 'bootstrap-vue/dist/bootstrap-vue.css'

// Make BootstrapVue available throughout your project
Vue.use(BootstrapVue)
// Optionally install the BootstrapVue icon components plugin
Vue.use(IconsPlugin)

// @ts-ignore
import SweetModal from 'sweet-modal-vue/src/plugin.js'

// @ts-ignore
import VuePageTitle from 'vue-page-title'

import { extend } from 'vee-validate';
import {email, max, min, numeric, required} from 'vee-validate/dist/rules';

import { ValidationProvider, ValidationObserver } from "vee-validate";
// @ts-ignore

const moment = require('moment')
require('moment/locale/es')

Vue.use(require('vue-moment'), {
  moment
})

Vue.component('ValidationProvider', ValidationProvider);
Vue.component('ValidationObserver', ValidationObserver);

import VueApexCharts from 'vue-apexcharts'
Vue.use(VueApexCharts)

Vue.component('apexchart', VueApexCharts)

Vue.use(SweetModal)

extend('required', {
  ...required,
  message: 'Este campo es requerido'
});
extend('', {
  ...required,
  message: 'Este campo es requerido'
});
extend("max", {
  ...max,
  message:"Este campo debe tener {length} caracteres o menos"
});

extend("min", {
  ...min,
  message:"Este campo debe tener {length} caracteres o más"
});

extend("email", {
  ...email,
  message: "Este campo debe ser un correo electrónico válido"
});

extend("numeric", {
  ...numeric,
  message: "Este campo debe ser numerico"
});

Vue.filter('toCurrency', function (value: number) {
  const formatter = new Intl.NumberFormat('de-CO', {
    style: 'currency',
    currency: 'COP',
    minimumFractionDigits: 0,


  });
  return formatter.format(value);
});

import VueSimpleAlert from "vue-simple-alert";

Vue.use(VueSimpleAlert);

Vue.config.productionTip = false

Vue.use(VuePageTitle, { router })


import * as VueGoogleMaps from 'vue2-google-maps'

Vue.use(
    VueGoogleMaps,
    {
      load: {
        key: "AIzaSyCf1eRq44hnrD1Y6QbfT3QQ-acOhPBN5Lc",
        libraries: 'places,drawing,visualization'
      },
       installComponents: true,
    }
);
// @ts-ignore
import {GmapMarker} from 'vue2-google-maps/src/components/marker'
// @ts-ignore
import {GmapInfoWindow} from 'vue2-google-maps/src/components/infoWindow'

// @ts-ignore
Vue.component('GmapMarker', GmapMarker)
// @ts-ignore
Vue.component('GmapInfoWindow', GmapInfoWindow)

// @ts-ignore
import VueLoading from 'vue-loading-overlay';
// Import stylesheet
import 'vue-loading-overlay/dist/vue-loading.css';
// Init plugin
Vue.use(VueLoading);
Vue.component('loading', VueLoading)


import {apolloProvider} from './apollo'

import { rtdbPlugin } from 'vuefire'

Vue.use(rtdbPlugin)



const vue = new Vue({
  router,
  store,
  apolloProvider,
  render: h => h(App)
}).$mount('#app')

Service.prototype.$vue = vue;
Service.prototype.$router = vue.$router;
